.status {
  color: gray;
  font-weight: bold;
  text-align: center;
  border: 1px solid black;
  padding: 10px;
  margin: 20px auto;
  width: fit-content;
}

.board {
  display: grid;
  margin-right: 800px;
  border: 1px solid black;
}
.square {
  width: 120px;
  height: 110px;
}

.square:hover {
  background-color: rgba(255, 255, 0, 0.3);
}

.square:active {
  background-color: rgba(255, 255, 0, 0.6);
}

.game-info {
  float: right;
  margin-left: 20px;
}

.game-info ol {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-top: -340px;
  margin-right: 400px;
}

.game-info li {
  margin-bottom: 5px;
}

.game-info button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
